import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
// import adCell from '@/component/ADCell/ADCell.vue';
import { post,tokenpost} from '@/api/user.js';
import { api } from '@/config/common.js';
export default {
	components: {
		// adCell
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '我的',
			dataList: {
				loginname:'xuewei',
				realname:'张三丰',
				telephone:'15827068282',
				companyname:'湖北吉奥汽车服务有限公司',
				expiredate:'2020-11-20',
				daycount:0,
				ordercount:'30'
			}
		};
	},
	onLoad(){},
	onShow(){
		if(!uni.getStorageSync('userInfo')){
			uni.reLaunch({
				url:'/pages/my/login/login'
			})
		};
		this.loadData();
	},
	methods: {
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handlePassword(){
			console.log("11111111111");
			uni.navigateTo({
				url: '/pages/my/login/editPassword'
			});
		},
		handleBankSet(){
			console.log("11111111111");
			uni.navigateTo({
				url: '/pages/my/account/accountlist'
			});
		},
		handleRecharge(){
			console.log("11111111111");
			uni.navigateTo({
				url: '/pages/my/account/recharge'
			});
		},
		handleUserManage(){
			console.log("11111111111");
			uni.navigateTo({
				url: '/pages/my/user/user'
			});
		},
		handleMyorder(){
			console.log("11111111111");
			uni.navigateTo({
				url: '/pages/my/myorder'
			});
		},
		handleGridChange(val) {
			switch (val.id) {
				case '1':
					uni.navigateTo({
						url: '../sale/sale'
					});
					break;
				case '2':
					uni.navigateTo({
						url: '../purchase/purchase'
					});
					break;
				case '3':
					uni.navigateTo({
						url: '../current_unit/current_unit'
					});
					break;
				case '4':
					uni.navigateTo({
						url: '../product/product'
					});
					break;
			}
		},
		//退出登录
		handleLogout() {
			uni.showModal({
				title: '确定要退出登录么？',
				success: e => {
					if (e.confirm) {
						try {
							uni.clearStorageSync();
							setTimeout(() => {
								const userInfo = uni.getStorageSync('userInfo');
								if (!userInfo) {
									uni.reLaunch({
										url: '/pages/my/login/login'
									});
								}
							}, 200);
						} catch (e) {}
					}
				}
			});
		},
		//设置
		handleSet(){
			uni.navigateTo({
				url: '../my/set'
			});
		},
		loadData(){
			// let userid = uni.getStorageSync('userInfo').userid;
			// let token = uni.getStorageSync('userInfo').token;
			// let url = 'http://120.210.132.94:5599/api/BseUser/GetUserInfo';
			tokenpost(api.GetUserInfo).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
				  this.dataList = res.data.data
				} else {
					this.$api.msg(res.data.returnMessage) 
				}
				this.loading =false;
			}).catch(error => {
				this.loading =false;
				this.$api.msg('请求失败fail') 
			})
			
		}
	}
};
